
   import {watch, defineComponent, reactive} from 'vue'
   import Joi, { boolean } from 'joi'
   import { useToast } from "vue-toastification"
   import emailjs from 'emailjs-com'
   import{ init } from 'emailjs-com';
   import date from 'date-and-time';
   import {formValidation} from '../validation/form_validation'
   import ArrayInput from '../components/ArrayInput.vue'


   export default defineComponent({
       name: 'Home',
       components:{ArrayInput},
       props:{
           horizontal:{
               type: boolean
           }
       },
       setup(props){
            
            // emailjs.com
           init("user_WjYj4YxrXX5vvj4wlw4nv");
           const ServiceID = 'cyfan'
           const TemplateID= 'template_t8exz6j'

           const toast = useToast()
           const timePeriod = reactive<Array<string>>([
               '08:10 ~ 11:30', 
               '13:00 ~ 16:30', 
               '17:00 ~ 22:00',
               '全天'
           ])

           const formInputData = reactive({
               classes: '',
               teacher: '',
               description: '',
               toolsRent: '',

               userMail: '',
               applyUser: {
                   name: '',
                   classNum: '',
                   studentID: '',
                   phone: ''
               },

               agentUser:{
                   name: '',
                   classNum: '',
                   studentID: '',
                   phone: ''
               },
               classMate:[],
               classMateString: '',
               applyDate: date.format(new Date(), 'YYYY-MM-DD'),
               rentDate: '',
               TimePeriod: '',
           })

            const selectPeriod = (e: Event): void =>{
                const target: HTMLDivElement = (e.target as HTMLDivElement)
                if(target.className === 'time-period-select') return

                if (target.innerText === '全天') {
                    Array.from(document.getElementsByClassName('time-period'))
                        .forEach(element => {
                            element.classList.remove('selected')
                        });
                    target.classList.add('selected')
                    formInputData.TimePeriod = '全天'
                    return
                }

                if(formInputData.TimePeriod === '全天'){
                    Array.from(document.getElementsByClassName('time-period'))
                        .forEach(element => {
                            element.classList.remove('selected')
                        });
                    formInputData.TimePeriod = ''

                }

                if(target.classList.contains('selected')){
                    target.classList.remove('selected')
                    formInputData.TimePeriod = formInputData.TimePeriod.replace(target.innerHTML + ' | ', '')
                }else{
                    target.classList.add('selected')
                    formInputData.TimePeriod += target.innerHTML + ' | '

                }

                
            }

            const checkDate = (e: Event): void =>{
                const rentDate = new Date((e.target as HTMLInputElement).value)
                const today = new Date(formInputData.applyDate)
                const sub = date.subtract(rentDate, today).toDays()
                const target = (document.getElementById('input_rentDate') as HTMLInputElement)
                if (sub<= 0 || sub > 21){
                    target.classList.add('input-invalid')
                }
                else{
                    target.classList.remove('input-invalid')
                }
            }
           


           const submit = async ()=>{

                // Check if cool down
                const lastSend: string | null  = localStorage.getItem('lastSend')
                if(lastSend){
                    const last = new Date(lastSend)
                    const sub = date.subtract(new Date, last).toMinutes()

                    if (sub < 10){
                        toast.warning(`申請提交冷卻中 ! 還需等待 ${10 - Math.round(sub)} 分鐘`)
                        return
                    }
                }

                formInputData.classMate.forEach((user: any)=>{
                    formInputData.classMateString += `【姓名: ${user.name}  /  班級座號: ${user.classNum}  /  學號: ${user.studentID}  /  電話: ${user.phone}】    ||    `
                })

                // Data Validate
               try {
                   const validation = await formValidation.validate(formInputData)
                   if(validation.error){
                       console.log(validation)
                       toast.error(validation.error.toString())
                       return
                   }
               } 
               catch (error) {
                   toast.error(error)
               }

                let inputInvalid = false
               Array.from(document.getElementsByTagName('input'))
                .forEach(element =>{
                    if(element.classList.contains('input-invalid')){
                        inputInvalid = true
                    }
                })

                if (inputInvalid) return
                
                //send email
                // todo: cooldown
                emailjs.send(ServiceID,TemplateID, formInputData)
                    .then(
                        (response): void=>{
                            console.log('SUCCESS!', response.status);
                            toast.success('已成功送出申請單，請靜待批准回覆信件')
                            const now = new Date()
                            localStorage.setItem('lastSend', now.toString())

                        }, 
                        (err): void=>{
                            toast.error(err)
                        }
                    );
           }

           watch(
               ()=>formInputData.userMail, 
               async (mail)=>{
                   const validation = Joi.object({
                        eMail: Joi.string()
                            .email({ tlds: {allow: false} })
                            .min(6)
                            .required()
                    })

                   try {
                       const va = await validation.validate({eMail: mail})
                           const target = (document.getElementById('input_email') as HTMLInputElement)
                       if (va.error) {
                            target.classList.add('input-invalid')
                       }
                       else{
                           target.classList.remove('input-invalid')
                       }
                   } catch (error) {
                       toast.success(error)
                   }
               }
            )


            const reload = (): void=>{
                const target: HTMLIFrameElement = (document.getElementById('Calendar') as HTMLIFrameElement)
                const nsrc = target.src
                target.src = nsrc
            }

          
           return{
               date,
               timePeriod,
               submit,
               selectPeriod,
               formInputData,
               checkDate,
               reload
               
           }
       }
   })
